import Axios from "axios";
export function getSearchParametersFromHRef(href) {
  if (!href) return {};
  var search = {};
  var s1 = href.split("?");
  if (s1.length > 1) {
    var s2 = s1[1].split("&");
    for (let index = 0; index < s2.length; index++) {
      const s3 = s2[index].split("=");
      search[s3[0]] = decodeURIComponent(s3[1]);
    }
  }
  return search;
}

export function getSearchParametersFromHash(href) {
  if (!href) return {};
  var search = {};
  var s1 = href.split("#");
  if (s1.length > 1) {
    var s2 = s1[1].split("&");
    for (let index = 0; index < s2.length; index++) {
      const s3 = s2[index].split("=");
      search[s3[0]] = decodeURIComponent(s3[1]);
    }
  }
  return search;
}

export function authenticate(config) {
  // https://docs.microsoft.com/en-us/azure/active-directory/develop/v2-oauth2-implicit-grant-flow

  return new Promise((resolve, reject) => {
    var redirect_uri =
    (window.location.hostname === "localhost" ? window.location.protocol : "https:") +
      "//" +
      window.location.hostname +
      (window.location.port !== 80 &&
      window.location.port !== 443 &&
      window.location.port !== ""
        ? ":" + window.location.port
        : "") +
      "/";
    // window.location.pathname;

    var host = `https://login.microsoftonline.com/${config.directoryId}/oauth2/v2.0/authorize`;
    var client_id = config.clientId;
    var response_type = "id_token";
    var response_mode = "fragment";
    var scope = config.scopes;

    var seq = sessionStorage.getItem("authseq");
    var search = getSearchParametersFromHash(window.location.href);

    var search2 = getSearchParametersFromHRef(window.location.href);
    //debugger
    if (search.id_token) {
      seq = "gotidtoken";
    } else if (search.access_token) {
      seq = "gotaccesstoken";
    } else if (seq === null) {
      seq = "login";
    }

    if (search.error || search2.error) {
      var e = search.error ? search.error : search2.error;

      switch (e) {
        case "interaction_required":
          sessionStorage.removeItem("accesstoken");
          sessionStorage.removeItem("idtoken");
          sessionStorage.removeItem("me");
          sessionStorage.setItem("authseq", "login");

          var url = `${host}?client_id=${client_id}&response_type=${response_type}&redirect_uri=${redirect_uri}&response_mode=${response_mode}&scope=openid&state=authenticate&nonce=123`;
          window.location.href = url;
          return;
          break;
        case "consent_required":
          //debugger;
          sessionStorage.removeItem("accesstoken");
          sessionStorage.removeItem("idtoken");
          sessionStorage.removeItem("me");
          sessionStorage.setItem("authseq", "login");

          var url = `${host}?client_id=${client_id}&prompt=consent&response_type=token&redirect_uri=${redirect_uri}&response_mode=${response_mode}&scope=openid&state=authenticate&nonce=123`;
          window.location.href = url;
          return;
          break;

        default:
          break;
      }
      alert(
        search.error_description
          ? search.error_description
          : search2.error_description
      );
      window.location.href = sessionStorage.getItem("returnto");

      return;
    }

    switch (seq) {
      case "authenticate":
        debugger
        break
      case "login":
        sessionStorage.setItem("authseq", "authenticate");
        sessionStorage.setItem("returnto",window.location.href)
        var authScope = ""
        scope.forEach(element => {
          authScope += encodeURIComponent(
            "https://graph.microsoft.com/" + element
          );
          authScope += " "
        });


        
        var url = `${host}?client_id=${client_id}&prompt=none&response_type=token&redirect_uri=${redirect_uri}&response_mode=${response_mode}&scope=${authScope}&state=authorize&nonce=123`;
        window.location.href = url;
        return;
        break;
      case "gotidtoken":
        
        sessionStorage.setItem("authseq", "authorize");
        var authScope = encodeURIComponent(
          "https://graph.microsoft.com/" + scope
        );
        var url = `${host}?client_id=${client_id}&prompt=none&response_type=token&redirect_uri=${redirect_uri}&response_mode=${response_mode}&scope=${authScope}&state=12345&nonce=123`;
        window.location.href = url;
        return;
        break;
      case "authorized":
        
        sessionStorage.removeItem("authseq");
        sessionStorage.removeItem("returnto");

        Axios.get("https://graph.microsoft.com/v1.0/me", {
          headers: {
            "content-type": "application/json",
            authorization: `Bearer ${sessionStorage.getItem("access_token")}`,
          },
        })
          .then((result) => {
            sessionStorage.setItem("me", JSON.stringify(result.data));
            resolve({user: result.data});
          })
          .catch((error) => {
            var txt = error.response.data.error.message;
            if (
              error &&
              error.response &&
              error.response.data &&
              error.response.data.error
            ) {
              //alert(error.response.data.error.message)
              resolve({ error: error.response.data.error.message });
            } else {
             ;
              resolve({error});
            }
          });
        break;

      case "gotaccesstoken":
        
        if (search.access_token) {
          sessionStorage.setItem("access_token", search.access_token);
          sessionStorage.setItem("expires_in", search.expires_in);

          sessionStorage.setItem("authseq", "authorized");
          window.location.href = sessionStorage.getItem("returnto");
          return;
        } else {
          sessionStorage.removeItem("authseq");
          window.location.reload();
        }
        resolve();
        break;

      default:
        resolve();
        break;
    }
  });
}
