import React, { useState, useEffect } from "react";
import _ from "lodash";
import config from "./config";
import { initializeIcons } from "@uifabric/icons";
//import AuthHelper from "./officegraph/authHelper";
//import OfficeGraph from "./officegraph";
import moment from "moment";
import {
  MessageBar,
  MessageBarType,
  PrimaryButton,
  Button,
  getScreenSelector,
  DATA_PORTAL_ATTRIBUTE,
} from "office-ui-fabric-react";
import "./App.css";
import packageData from "../package.json";
import axios from "axios";
initializeIcons();

function inIframe() {
  try {
    return window.self !== window.top;
  } catch (e) {
    return true;
  }
}

/******************************************************************************************************************************
 *
 * Proxied class
 *
 ******************************************************************************************************************************/
class ZTICKYBAR {
  static init = () => {
    return new ZTICKYBAR();
  };
  constructor() {
    this._key = "123";
    this._targetOrigin = "*";
  }

  get openerHref() {
    return this._openerHref;
  }
  get tileId() {
    return this._tileId;
  }
}

/**
 *
 *
 *
 */
export default function Auth(props) {
  const [errors, setErrors] = useState([]);

  const inFrame = inIframe();
  const [accessToken, setAccessToken] = useState("");
  const [refresh, setRefresh] = useState(0);

  /******************************************************************************************************************************
   *
   * Update the UI when necessary
   *
   ******************************************************************************************************************************/

  useEffect(() => {}, [accessToken, refresh]);
  /******************************************************************************************************************************
   * ******************************************************************************************************************************
   *
   *     MAIN display
   *
   *******************************************************************************************************************************
   ******************************************************************************************************************************/

  return (
    <div style={{ position: "relative", height: "95vh" }}>
      {/**********************************************************************************************************************
       *
       * If the page is not shown in an iFrame, show a custom header
       *
       *********************************************************************************************************************/}
      {!inFrame &&
        config.components &&
        config.components.header &&
        config.components.header}
      {/**********************************************************************************************************************
       *
       * If errors has occured, show on at a time
       *
       *********************************************************************************************************************/}
      {errors.length > 0 && (
        <MessageBar
          messageBarType={MessageBarType.error}
          isMultiline={false}
          onDismiss={() => {
            setErrors(errors.slice(1));
          }}
          dismissButtonAriaLabel="Close"
        >
          Error: {JSON.stringify(errors[0])}
        </MessageBar>
      )}
      <div style={{ backgroundColor: "#eeeeee" }}>
        <div
          style={{
            padding: "20px",
            marginLeft: "auto",
            marginRight: "auto",
            maxWidth: "1280px",
            backgroundColor: "#ffffff",
            height: "80vh",
          }}
        >
          {props && props.auth && props.auth.user && (
            <div>
              <h1>Welcome {props.auth.user.displayName}</h1>
              Your are signed in as "{props.auth.user.userPrincipalName}". Visit your Office profile at &nbsp;
          <a href="https://portal.office.com/account/#apps" target="_blank">
            https://portal.office.com/account/#apps
          </a>
            </div>
          )}


{props && props.auth && props.auth.error && (
            <div>
              <h1>Sorry - You cannot get in here</h1>
             {JSON.stringify(props.auth.error)}
            </div>
          )}


        </div>
      </div>
    </div>
  );
}
